<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Tenures<sup><a href="javascript:void(0)" class="text-dark" @click.prevent="newGenerationView" title="Create Generation"><i class="mdi mdi-plus-circle"></i></a></sup></h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                        <li class="breadcrumb-item"><router-link to="/console" >Home</router-link></li>
                        <li class="breadcrumb-item active">Tenures</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">
                
                <div class="container-fluid" >
                    

                    <!-- Generations -->
                    <div class="pb-5" v-if="NewGenerationView === false">
                        
                        <div class="table-responsive">
                            <b-row class="mb-3">
                                <b-col md="3" class="col-6">
                                    <b-form-input v-model="filter" type="search" id="filterInput" placeholder="Type to Search"></b-form-input>
                                </b-col>
                                <b-col md="9" class="col-6 text-end">
                                    <b-button @click="refreshGenerations" class="btn bn-dark"><i class="mdi mdi-refresh"></i> Refresh</b-button>
                                </b-col>
                            </b-row>

                            <b-row>
                                <b-col>
                                    <b-table striped hover outlined
                                        :filter="filter"
                                        :items="generations"
                                        :fields="fields"
                                        :per-page="perPage"
                                        :current-page="currentPage"
                                    >
                                        <template #cell(tenure)="row">
                                            {{row.item.from}}/{{row.item.to}} 
                                            <sup v-if="row.item.status === 1">
                                                <span class="badge badge-success">Current</span>
                                            </sup>
                                        </template>
                                        <template #cell(president)="row">
                                            {{row.item.user !== null ? `${row.item.user.first_name} ${row.item.user.last_name}` : ''}}
                                        </template>
                                        <template #cell(action)="row">
                                            <b-button size="sm" @click="filterGeneration(row.item.id)" class="mr-2 mb-2">
                                                <i class="mdi mdi-pencil"></i>
                                            </b-button>
                                            <b-button size="sm" class="btn btn-success mb-2" @click="loadTenureExecutivesView(row.item.id)" title="View Details"><i class="mdi mdi-arrow-right"></i></b-button>
                                        </template>
                                        <template #cell(date)="row">
                                            {{row.item.createdAt | moment("dddd, MMMM Do YYYY")}}
                                        </template>
                                    </b-table>
                                    <b-pagination
                                        v-model="currentPage"
                                        :total-rows="generations.length"
                                        :per-page="perPage"
                                        aria-controls="my-table"
                                    ></b-pagination>
                                </b-col>
                            </b-row>
                        </div>
                        <!-- <b-table striped hover outlined :items="posts"></b-table> -->

                        <div v-if="generations.length < 1" class="text-start">
                            <b>No generation added yet!</b>
                        </div>

                    </div>

                    <div v-else >
                    
                        <!-- Create Generation -->
                        <div v-if="Generation === null">
                            <form method="POST" @submit.prevent="handleNewGeneration">
                                <h3 class="text-start">
                                    <a href="javascript:void(0)" class="text-dark" @click="loadGenerationView()"><i class="mdi mdi-chevron-left"></i></a> 
                                    Create a new generation
                                </h3>
                                <div class="pb-5">
                
                                    <div class="col-xl-6 col-md-12 my-4">
                            
                                        <div class="form-group text-start">
                                            <label for="">Name</label>
                                            <input type="text" v-model="form.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                        </div>
                                            
                                        <div class="row form-group text-start">
                                            <div class="col">
                                                <label for="">From</label>
                                                <input type="text" v-model="form.from" class="form-control remove-radius" required />
                                            </div>
                                            <div class="col">
                                                <label for="">To</label>
                                                <input type="text" v-model="form.to" class="form-control remove-radius" required />
                                            </div>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Description (Optional)</label>
                                            <textarea v-model="form.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">President</label>
                                            <select v-model="form.head_id" class="custom-select remove-radius" required>
                                                <option value="" selected disabled>--Select president--</option>
                                                <option v-for="user in users" :key="user.id" :value="user.id">{{`${user.first_name} ${user.last_name}`}}</option>
                                            </select>
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Visibility</label>
                                            <select v-model="form.status" class="custom-select remove-radius" required>
                                                <option value="1">True</option>
                                                <option value="0">False</option>
                                            </select>
                                        </div>
                
                                    </div>

                                    <div class="col-xl-6 col-md-12 mb-2 text-start">
                                        <button class="btn btn-success" type="submit"><i class="fa fa-save"></i> Save</button>
                                    </div>

                                </div>

                            </form>
                        </div>

                        <!-- Update Generation -->
                        <div v-else >

                            <div class="pb-5" v-if="TenureExecutivesView === false">
                                <form method="POST" @submit.prevent="handleGenUpdateForm">
                                    <h3 class="text-start">
                                        <a href="javascript:void(0)" class="text-dark" @click="loadGenerationView()"><i class="mdi mdi-chevron-left"></i></a> 
                                        Update generation
                                    </h3>  
                                    <div class="pb-5">
                    
                                        <div class="col-xl-6 col-md-12 my-4">
                                
                                            <div class="form-group text-start">
                                                <input type="file" name="file_holder" ref="image" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                                <label for="profile_image" style="cursor: pointer;" v-if="Generation.image === null" ><img src="@/assets/images/image.png" class="img-fluid" style="width: 5em;" /></label>
                                                <label for="profile_image" style="cursor: pointer;" v-else ><img :src="Generation.image" class="img-fluid" style="width: 5em;"/></label>
                                            </div>

                                            <div class="form-group text-start">
                                                <label for="">Name</label>
                                                <input type="text" v-model="Generation.name" class="form-control remove-radius" placeholder="Enter name" required/>
                                            </div>
                                                
                                            <div class="row form-group text-start">
                                                <div class="col">
                                                    <label for="">From</label>
                                                    <input type="text" v-model="Generation.from" class="form-control remove-radius" required />
                                                </div>
                                                <div class="col">
                                                    <label for="">To</label>
                                                    <input type="text" v-model="Generation.to" class="form-control remove-radius" required />
                                                </div>
                                            </div>

                                            <div class="form-group text-start">
                                                <label for="">Description (Optional)</label>
                                                <textarea v-model="Generation.description" class="form-control remove-radius" placeholder="Enter description"></textarea>
                                            </div>

                                            <div class="form-group text-start">
                                                <label for="">President</label>
                                                <select v-model="Generation.head_id" class="custom-select remove-radius" required>
                                                    <option value="" selected disabled>--Select president--</option>
                                                    <option v-for="user in users" :key="user.id" :value="user.id">{{`${user.first_name} ${user.last_name}`}}</option>
                                                </select>
                                            </div>

                                            <div class="form-group text-start">
                                                <label for="">Visibility</label>
                                                <select v-model="Generation.status" class="custom-select remove-radius" required>
                                                    <option value="1">True</option>
                                                    <option value="0">False</option>
                                                </select>
                                            </div>
                    
                                        </div>

                                        <div class="col-xl-6 col-md-12 mb-2 text-start">
                                            <button class="btn btn-success mr-2" type="submit"><i class="mdi mdi-content-save"></i> Save</button>
                                            <button class="btn btn-danger" type="button" @click="handleDeleteGeneration(Generation.id)"><i class="mdi mdi-delete"></i> Delete</button>
                                        </div>

                                    </div>

                                </form>
                            </div>

                            <div v-else >

                                <div v-if="Executive === null">
                                    
                                    <div v-if="unit_details === null" >
                                        <h3 class="text-start">
                                            <a href="javascript:void(0)" class="text-dark" @click="loadGenerationView()"><i class="mdi mdi-chevron-left"></i></a> 
                                            Add excos to office ({{`${Generation.from}/${Generation.to}`}})
                                        </h3>

                                        
                                        <div v-if="offices.length < 1" class="text-start">
                                            <b>No Offices added yet!</b>
                                        </div>

                                        <div class="pb-5" v-else >

                                            <form method="post" @submit.prevent="handleNewExcoForm">
                                                <div class="form-group row pt-3">
                                                    <div class="col">
                                                        <select class="custom-select" v-model="executive_form.office_id" required>
                                                            <option value="" disabled selected>--Select post--</option>
                                                            <option v-for="office in offices" v-bind:key="office.id" :value="office.id">{{office.post}}</option>
                                                        </select>
                                                    </div>
                                                    <div class="col">
                                                        <select class="custom-select" v-model="executive_form.user_id" required>
                                                            <option value="" disabled selected>--Select user--</option>
                                                            <option v-for="user in users" v-bind:key="user.id" :value="user.id">{{user.first_name}} {{user.last_name}}</option>
                                                        </select>
                                                    </div>
                                                    
                                                    <div class="col text-start">
                                                        <button type="submit" class="btn btn-success mr-2" ><i class="mdi mdi-plus-circle"></i> Add exco</button>
                                                    </div>
                                                        
                                                </div>
                                            </form>

                                            <div class="row pb-5">
                                                <div class="col-xl-4 col-sm-6 col-6 h-100" v-for="executive in gen_executives" v-bind:key="executive.id">
                                                    <!-- small box -->
                                                    <div class="small-box bg-dark h-100 pt-2">
                                                        
                                                        <div class="inner">
                                                            
                                                            <div v-if="executive.user.profile_picture === null || executive.user.profile_picture === ''">
                                                                <img src="@/assets/images/profile.png"  class="img-fluid img-cover" />
                                                            </div>
                                                            <div v-else >
                                                                <img :src="executive.user.profile_picture" class="img-fluid img-cover" />
                                                            </div>
                                                            <p :title="`${executive.user.first_name} ${executive.user.last_name}`">{{`${executive.user.first_name} ${executive.user.last_name}`}}</p>
                                                            <h5 class="text-truncate" :title="executive.office.post">{{executive.office.post}}</h5>
                                                            
                                                        </div>
                                                        <div class="small-box-footer text-end mb-0 p-2">
                                                            <a href="javascript:void(0)" class="btn btn-success btn-sm mr-2" @click="filterExecutive(executive.id)">Edit</a>
                                                            <a href="javascript:void(0)" class="btn btn-danger btn-sm" @click="handleDeleteExecutive(executive.id)">Cancel</a>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>

                                        </div>
                                    </div>

                                    <div v-else>
                                        <div class="row">
                                            <div class="col-12">
                                                <h3 class="text-start">
                                                    <a href="javascript:void(0)" class="text-dark" @click="loadGenerationView()"><i class="mdi mdi-chevron-left"></i></a> 
                                                    Add members to unit ({{`${Generation.from}/${Generation.to}`}})
                                                </h3>
                                                <h5 class="text-start font-weight-bold">
                                                    {{unit_details.name}}
                                                    <sup><router-link to="/console/users" class="text-dark" title="Create users"><i class="mdi mdi-plus-circle"></i></router-link></sup>
                                                </h5>

                                                <form method="post" @submit.prevent="handleNewUnitDetailsForm">
                                                    <div class="form-group row">
                                                        <div class="col">
                                                            <select class="custom-select" v-model="unit_details_form.role" @change="usersByRole" required>
                                                                <option value="" disabled selected>--Select role--</option>
                                                                <option value="exco">Executive</option>
                                                                <option value="sub-exco">Sub-executive</option>
                                                                <option value="member">Member</option>
                                                            </select>
                                                        </div>
                                                        <div class="col">
                                                            <select class="custom-select" v-model="unit_details_form.user_id" required>
                                                                <option value="" disabled selected>--Select user--</option>
                                                                <option v-for="user in users_by_role" v-bind:key="user.id" :value="user.id">{{user.first_name}} {{user.last_name}}</option>
                                                            </select>
                                                        </div>
                                                        
                                                        <div class="col text-start">
                                                            <button type="submit" class="btn btn-success mr-2" ><i class="mdi mdi-plus-circle"></i> Add </button>
                                                        </div>
                                                            
                                                    </div>
                                                </form>
                                            </div>
                                            <div class="col-xl-3 col-12">

                                                <div class="card">
                                                    <div class="card-body">
                                                        <h5 class="mb-0">Executive(s)</h5>
                                                    </div>
                                                    <div class="card-body">
                                                        <div class="row pb-5">
                                                            
                                                            <div class="col-6 col-xl-12 h-100" v-for="unit_detail in unit_excos" v-bind:key="unit_detail.id">
                                                                <!-- small box -->
                                                                <div class="small-box bg-dark pt-2">
                                                                    
                                                                    <div class="inner">
                                                                        <img src="@/assets/images/profile.png" v-if="unit_detail.exco.profile_picture === null" class="img-fluid" style="width: 5em;" />
                                                                        <img src="@/assets/images/profile.png" v-else-if="unit_detail.exco.profile_picture === ''" class="img-fluid" style="width: 5em;" />
                                                                        <img :src="unit_detail.exco.profile_picture" class="img-fluid" style="width: 5em;" />
                                                                        <p class="mb-0" :title="`${unit_detail.exco.first_name} ${unit_detail.exco.last_name}`">{{`${unit_detail.exco.first_name} ${unit_detail.exco.last_name}`}}</p>
                                                                        <p class="mb-0">{{unit_detail.exco.dept}}</p>
                                                                        <!-- <h5 class="text-truncate" :title="executive.office.post">{{executive.office.post}}</h5> -->
                                                                        
                                                                    </div>
                                                                    <div class="small-box-footer text-end mb-0 p-2">
                                                                        <!-- <a href="javascript:void(0)" class="btn btn-success btn-sm mr-2" @click="filterExecutive(unit_detail.id)">Edit</a> -->
                                                                        <a href="javascript:void(0)" class="btn btn-danger btn-sm" @click="handleDeleteUnitMember(unit_detail.id)">Remove</a>
                                                                    </div>
                                                                </div>

                                                            </div>

                                                            <div class="col-12" v-if="unit_excos.length === 0">
                                                                No excos
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                

                                                <!-- <img src="@/assets/images/image.png" v-if="Executive.user.profile_picture === null" class="img-fluid" style="width: 5em;" /></label>
                                                <img :src="Generation.image" v-else class="img-fluid" style="width: 5em;"/> -->

                                            </div>
                                            <div class="col-xl-9 col-12">
                                                
                                                <div class="card">
                                                    <div class="card-header text-start">
                                                        <h5 class="mb-0">Sub Excos</h5>
                                                    </div>
                                                    <div class="card-body">
                                                        <div class="row pb-5">
                                                            <div class="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-6 h-100" v-for="unit_detail in unit_sub_excos" v-bind:key="unit_detail.id">
                                                                <!-- small box -->
                                                                <div class="small-box bg-dark h-100 pt-2">
                                                                    
                                                                    <div class="inner">
                                                                        <img src="@/assets/images/profile.png" v-if="unit_detail.sub_exco.profile_picture === null || unit_detail.sub_exco.profile_picture === ''" class="img-fluid" style="width: 5em;" />
                                                                        <img :src="unit_detail.sub_exco.profile_picture" class="img-fluid" style="width: 5em;" />
                                                                        <p class="mb-0" :title="`${unit_detail.sub_exco.first_name} ${unit_detail.sub_exco.last_name}`">{{`${unit_detail.sub_exco.first_name} ${unit_detail.sub_exco.last_name}`}}</p>
                                                                        <p class="mb-0">{{unit_detail.sub_exco.dept}}</p>
                                                                        <!-- <h5 class="text-truncate" :title="executive.office.post">{{executive.office.post}}</h5> -->
                                                                        
                                                                    </div>
                                                                    <div class="small-box-footer text-end mb-0 p-2">
                                                                        <a href="javascript:void(0)" class="btn btn-danger btn-sm" @click="handleDeleteUnitMember(unit_detail.id)">Remove</a>
                                                                    </div>
                                                                </div>

                                                            </div>

                                                            <div class="col-12 text-start" v-if="unit_sub_excos.length === 0">
                                                                No sub excos
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div class="card">
                                                    <div class="card-header text-start">
                                                        <h5 class="mb-0">Members</h5>
                                                    </div>
                                                    <div class="card-body">
                                                        <div class="row pb-5">
                                                            
                                                            <div class="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-6 h-100" v-for="unit_detail in unit_members" v-bind:key="unit_detail.id">
                                                                <!-- small box -->
                                                                <div class="small-box bg-dark pt-2">
                                                                    
                                                                    <div class="inner">
                                                                        <img src="@/assets/images/profile.png" v-if="unit_detail.member.profile_picture === null" class="img-fluid" style="width: 5em;" />
                                                                        <img src="@/assets/images/profile.png" v-else-if="unit_detail.member.profile_picture === ''" class="img-fluid" style="width: 5em;" />
                                                                        <img :src="unit_detail.member.profile_picture" class="img-fluid" style="width: 5em;" />
                                                                        <p class="mb-0" :title="`${unit_detail.member.first_name} ${unit_detail.member.last_name}`">{{`${unit_detail.member.first_name} ${unit_detail.member.last_name}`}}</p>
                                                                        <p class="mb-0">{{unit_detail.member.dept}}</p>
                                                                        <!-- <h5 class="text-truncate" :title="executive.office.post">{{executive.office.post}}</h5> -->
                                                                        
                                                                    </div>
                                                                    <div class="small-box-footer text-end mb-0 p-2">
                                                                        <!-- <a href="javascript:void(0)" class="btn btn-success btn-sm mr-2" @click="filterExecutive(unit_detail.id)">Edit</a> -->
                                                                        <a href="javascript:void(0)" class="btn btn-danger btn-sm" @click="handleDeleteUnitMember(unit_detail.id)">Remove</a>
                                                                    </div>
                                                                </div>

                                                            </div>

                                                            <div class="col-12 text-start" v-if="unit_members.length === 0">
                                                                No members
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                    </div>


                                </div>

                                <div v-else >
                                    
                                    <div class="pb-5 row">
                                        <div class="col-xl-6 col-12">
                                            <h3 class="text-start">
                                                <a href="javascript:void(0)" class="text-dark" @click="loadTenureExecutivesView(Generation.id)"><i class="mdi mdi-chevron-left"></i></a> 
                                                Update executive
                                            </h3>
                                            
                                            <form method="post" class="text-start mt-3" @submit.prevent="handleExcoUpdateForm">
                                                <div class="form-group">
                                                    <label for="">Post</label>
                                                    <p>{{Executive.office.post}}</p>
                                                </div>

                                                <div class="form-group">
                                                    <label for="">Executive</label>
                                                    <select class="custom-select" v-model="Executive.user_id" required>
                                                        <option value="" disabled selected>--Select user--</option>
                                                        <option v-for="user in users" v-bind:key="user.id" :value="user.id">{{user.first_name}} {{user.last_name}}</option>
                                                    </select>
                                                </div>
                                                    
                                                <button type="submit" class="btn btn-success mr-2" ><i class="mdi mdi-save"></i> Update exco</button>
                                                
                                            </form>
                                        </div>
                                    </div>

                                </div>

                            </div>

                        </div>
                        
                    </div>

                </div>

            </section>
            <!-- /.content -->
        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";
import { mapGetters, mapActions } from 'vuex';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService'; 

export default {
    name: "ConsoleExecutives",
    data(){
        return {
            users: this.$store.getters.users.filter(user => user.role === 'admin'),
            members: this.$store.getters.users.filter(user => user.role !== 'admin'),
            users_by_role: [],
            image: '',
            filter: '',
            NewGenerationView: false,
            Generation: null,
            TenureExecutivesView: false,
            Executives: [],
            Executive: null,
            fields: [
                {
                    key: 'name',
                    sortable: true
                },
                {
                    key: 'tenure',
                    sortable: true
                },
                {
                    key: 'president',
                    sortable: true
                },
                {
                    key: 'date',
                    sortable: true
                },
                {
                    key: 'action',
                    sortable: false
                }
            ],
            form: {
                name: '', from: '', 
                to: '', description: '', 
                image: null, head_id: null, 
                status: 1 
            },
            executive_form: {
                user_id: '',
                office_id: '',
                generation_id: ''
            },
            unit_details_form: {
                role: '',
                user_id: '',
            },
            Unit_details_data: {
                executive_id: null,
                sub_exco_id: null,
                user_id: null,
                gen_id: null,
                unit_id: null
            },
            unit_excos: [],
            unit_sub_excos: [],
            unit_members: [],
            perPage: 20,
            currentPage: 1,
            available_offices: []
        }
    },
    computed: mapGetters(['unit_details', 'unit_info_details', 'generations', 'gen_executives', 'offices']),
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {
        ...mapActions(['deleteGeneration', 'createGeneration', 'createExecutive', 'updateGeneration', 'fetchOffices', 'fetchGenerationExecutives', 'deleteExecutive', 'updateExecutive', 'fetchUnitDetails', 'fetchUnitInfoDetails', 'createUnitInfoDetail', 'deleteUnitInfoDetail', 'updateUnitDetail']),
        async handleNewGeneration(){
            await this.createGeneration(this.form);
            await this.refreshGenerations();
            this.NewGenerationView = false;
        },
        
        async newGenerationView(){
            this.NewGenerationView = true;
            this.Generation = null;
        },
        async loadGenerationView(){
            this.NewGenerationView = false
            this.Generation = null;
        },
        async filterGeneration(id){
            this.Generation = this.generations.filter(generation => generation.id === id);
            if (this.Generation.length > 0) {
                this.Generation = this.Generation[0];
                this.NewGenerationView = null;
                this.TenureExecutivesView = false;
            }
        },
        
        async refreshGenerations(){
            await this.$store.dispatch('fetchGenerations');
            await this.fetchUnitDetails(-1);
        },
        async handleGenUpdateForm(){
            try{
                await this.updateGeneration({id: this.Generation.id, generation: this.Generation});
                await this.fetchGenerations();
                this.unit_details = null;
            }
            catch(err){
                console.log(err);
            }
        },
        async handleDeleteGeneration(id){
            let generation = this.generations.filter(generation => generation.id === id);
            this.unit_details = null;
            // console.log(generation);
            if (generation.length > 0) {
                if (generation[0].image === null || generation[0].image === '') {
                    await this.deleteGeneration(id);
                    await this.refreshGenerations();
                }
                else{
                    const response = await AuthenticationService.removeImage({old_image: generation[0].image});
                    if(response.data.error === 0){
                        await this.deleteGeneration(id);
                        await this.refreshGenerations();
                    }
                }
            }
        },
        async changeImage(){
            this.image = this.$refs.image.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'office');
            formData.append('tags', 'unsigned upload, office');
            formData.append('file', this.image);

            try {
                let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);
    
                if (response.data.secure_url !== null) {
                    this.Generation.image = response.data.secure_url;
                    let old_image = this.Generation.image;  
                    await this.update_account({id: this.Generation.id, user: this.Generation});
    
                    if (old_image !== null) {
                        await AuthenticationService.removeImage({old_image});
                    }
    
                }
                else{
                    alert("Bad network")
                }
            }
            catch(err){
                alert("Bad network")
            }
        },

        async loadTenureExecutivesView(id){
            this.Executive = null;
            this.NewGenerationView = true;
            this.TenureExecutivesView = true;
            this.Generation = this.generations.filter(generation => generation.id === id);
            if (this.Generation.length > 0) {
                if (this.unit_details !== null) {
                    await this.fetchUnitInfoDetails({gen_id: this.Generation[0].id, unit_id: this.unit_details.id});
                    this.unit_excos = this.unit_info_details.filter(unit_detail => unit_detail.exco !== null);
                    this.unit_sub_excos = this.unit_info_details.filter(unit_detail => unit_detail.sub_exco !== null);
                    this.unit_members = this.unit_info_details.filter(unit_detail => unit_detail.member !== null);
                    // console.log(this.unit_info_details);
                }
                this.available_offices = [];
                this.Generation = this.Generation[0];
                this.executive_form.generation_id = this.Generation.id
                await this.fetchGenerationExecutives(this.Generation.id);
            }
            
        },
        async handleNewExcoForm(){
            await this.createExecutive(this.executive_form);
        },
        async handleDeleteExecutive(id){
            let gen_executive = this.gen_executives.filter(gen_executive => gen_executive.id === id);
            if (gen_executive.length > 0) {
                await this.deleteExecutive(id);
            }
            
        },
        async filterExecutive(id){
            this.Executive = this.gen_executives.filter(executive => executive.id === id);
            if (this.Executive.length > 0) {
                this.Executive = this.Executive[0];
            }
        },
        async handleExcoUpdateForm(){
            try{
                const Exco = {
                    user_id: this.Executive.user_id
                }
                await this.updateExecutive({id: this.Executive.id, executive: Exco});
            }
            catch(err){
                console.log(err);
            }
        },

        async usersByRole(){
            const {role} = this.unit_details_form;
            if (role === 'exco') {
                this.users_by_role = this.users;
            }
            else{
                this.users_by_role = this.members;
            }
        },

        async handleNewUnitDetailsForm(){
            const {role, user_id} = this.unit_details_form;
            
            switch (role) {
                case 'exco':
                    this.Unit_details_data.executive_id = user_id;
                    break;
                case 'sub-exco':
                    this.Unit_details_data.sub_exco_id = user_id;
                    break;
                case 'member':
                    this.Unit_details_data.user_id = user_id;
                    break;
                default:
                    break;
            }
            this.Unit_details_data.unit_id = this.unit_details.id;
            this.Unit_details_data.gen_id = this.Generation.id;

            await this.createUnitInfoDetail(this.Unit_details_data);
            const {gen_id, unit_id} = this.Unit_details_data;   
            await this.fetchUnitInfoDetails({gen_id, unit_id});
            this.unit_excos = this.unit_info_details.filter(unit_detail => unit_detail.exco !== null);
            this.unit_sub_excos = this.unit_info_details.filter(unit_detail => unit_detail.sub_exco !== null);
            this.unit_members = this.unit_info_details.filter(unit_detail => unit_detail.member !== null);

            // Clear Unit details data
            this.Unit_details_data.executive_id = null;
            this.Unit_details_data.sub_exco_id = null;
            this.Unit_details_data.user_id = null;
            this.Unit_details_data.unit_id = null;
            this.Unit_details_data.gen_id = null;

        },

        async handleDeleteUnitMember(id){
            let unit_info_detail = this.unit_info_details.filter(unit_info_detail => unit_info_detail.id === id);
            if (unit_info_detail.length > 0) {
                await this.deleteUnitInfoDetail(id);  
                this.unit_excos = this.unit_info_details.filter(unit_detail => unit_detail.exco !== null);
                this.unit_sub_excos = this.unit_info_details.filter(unit_detail => unit_detail.sub_exco !== null);
                this.unit_members = this.unit_info_details.filter(unit_detail => unit_detail.member !== null);
                // await this.fetchUnitInfoDetails({gen_id: unit_info_detail[0].gen_id, unit_id: unit_info_detail[0].unit_id});
            }
        }

    },
    created(){
        this.fetchOffices();
        
    }
}
</script>

